<template>
  <div class="bg-white px-5 py-4 rounded-md flex flex-col gap-4">
    <h2>Xayriya qo‘shish</h2>
    <form class="grid grid-cols-5 gap-4" @submit="onSubmit">
      <div class="form_wrapper">
        <input v-validate-cyrillic id="name" v-model="name" name="name" class="base_input" placeholder="F.I.Sh" />
        <small>{{ nameError }}</small>
      </div>
      <div class="form_wrapper">
        <DateComponent
          v-model="date"
          :options="{
            maxDate: dayjs().endOf('year').format('YYYY-MM-DD'),
          }"
          placeholder="Sana"
        />
        <small>{{ dateError }}</small>
      </div>
      <div class="form_wrapper">
        <input v-validate-cyrillic id="amount" v-model="amount" type="number" name="amount" class="base_input" placeholder="Summa" />
        <small>{{ amountError }}</small>
      </div>
      <div>
        <button type="submit" class="text-white bg-[#4785FE] px-[12px] py-[10px] flex gap-[17px] item-center rounded-[5px] tracking-wide">Xayriya qo‘shish</button>
      </div>
    </form>
  </div>
</template>

<script setup>
import dayjs from 'dayjs';
import {useField, useForm} from 'vee-validate';
import {useRoute, useRouter} from 'vue-router';
import * as yup from 'yup';
import DateComponent from '../components/Common/DateComponent.vue';
import useAlert from '../composables/useAlert';
import useDonations from '../composables/useDonations';
import useScrollToError from '../composables/useScrollToError';
import {requiredValidatorMessage} from '../utils/validatorMessages';

function uuidv4() {
  return ([1e7] + -1e3 + -4e3 + -8e3 + -1e11).replace(/[018]/g, (c) => (c ^ (crypto.getRandomValues(new Uint8Array(1))[0] & (15 >> (c / 4)))).toString(16));
}

const router = useRouter();
const route = useRoute();

const {createDonation} = useDonations();
const {alertIt} = useAlert();
const {scrollToError} = useScrollToError();
const {handleSubmit, resetForm} = useForm();

function onInvalidSubmit({errors}) {
  console.log(Object.keys(errors));
  scrollToError(Object.keys(errors)[0]);
}

const onSubmit = handleSubmit(async (data) => {
  console.log(dayjs().toISOString(data.date));
  const resp = await createDonation({...data, date: dayjs().toISOString(data.date).split('T')[0]});
  console.log(resp);
  alertIt(resp.message);
  router.push({name: route.name, query: {id: uuidv4()}});
  resetForm()
}, onInvalidSubmit);

const {value: name, errorMessage: nameError} = useField('name', yup.string().required(requiredValidatorMessage), {initialValue: ''});
const {value: date, errorMessage: dateError} = useField('date', yup.string().nullable().required(requiredValidatorMessage), {initialValue: ''});

const {value: amount, errorMessage: amountError} = useField('amount', yup.string().required(requiredValidatorMessage), {initialValue: ''});
</script>

